.Sidebar {
  width: 60px;
  background-color: #f3f3f3;
  padding: 0.5rem 0; 
  display: -ms-flexbox; 
  display: flex; 
  -ms-flex-direction: column; 
      flex-direction: column; 
  -ms-flex-align: center; 
      align-items: center;
}
.Sidebar .logo {
  font-family: "Fauna One";
  color: #666;
  font-size: 3rem;
}
.Sidebar .logo img {
  width: 30px;
  padding-left: 4px;
}

.Sidebar button.new-note {
  margin-top: 2rem;
  position: relative;
  width: 40px;
  outline: none;
}
.Sidebar button {
  background-color: transparent;
  border: 0;
  color: #008BF8;
  cursor: pointer;
}
.Sidebar button img {
  position: absolute;
  left: 0;
  width: 100%;
  -webkit-transition: opacity 0.25s ease-in-out;
  -o-transition: opacity 0.25s ease-in-out;
  transition: opacity 0.25s ease-in-out;
}
.Sidebar a:hover img.outline {
  opacity: 0;
}

.SignOut {
  position: absolute;
  bottom: 1rem;
}
.SignOut button {
  outline: none;
}
.SignOut button i.fa {
  font-size: 2.5rem;
}


